.App {
  text-align: center;
}

.App-logo {
  height        : 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height      : 100vh;
  display         : flex;
  flex-direction  : column;
  align-items     : center;
  justify-content : center;
  font-size       : calc(10px + 2vmin);
  color           : white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

* {
  box-sizing: border-box;
  padding   : 0;
  margin    : 0;
}

html {
  /* set 1rem to 10px instead of native 16px to make it easier to calculate font-size values for the rest of the page (e.g. 1.6rem is 16px, 2.0rem is 20px, etc.) */
  font-size: 62.5%;
}

body {
  background-color: #d9dcd6;
}

header {
  display         : flex;
  flex-direction  : row;
  flex-wrap       : nowrap;
  padding         : 2vw;
  font-family     : 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  background-color: #2a607c;
  color           : #ffffff;
  justify-content : space-between;
}

header h1 {
  display  : inline-block;
  font-size: 4.8rem;
}


header a {
  float      : right;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

header div ul {
  list-style-type: none;
  margin-right   : 0.5vw;
}

header div ul li {
  display    : inline-block;
  margin-left: 2vw;
}

a {
  color          : #ffffff;
  text-decoration: none;

}

a:hover {
  color: black;
}

a:active {
  color: rgba(131, 159, 185, 0.336);
}

.container {
  max-width: 80vw;
}

.text-square h2 {
  font-weight: lighter;
  color      : #2a607c;
}

i {
  list-style    : none;
  font-size     : 30px;
  border-radius : 400px;
  width         : 50px;
  height        : 50px;
  display       : block;
  text-align    : center;
  padding-top   : 4px;
  padding-bottom: 0px;
  color         : #2c3e50;
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 601px) {
  .back-profile {
    background-image: url(/public/img/new_profile.jpg); 
    background-size : 100% 100%;
    height          : 15rem;
    margin-bottom   : 1.5rem
  }

  header h1.page-title {
    font-size: 3vw;
  }

  header a {
    padding-top: 0.5vw;
    font-size  : 2vw;
  }

  .left-column h2 {
    font-size: 3vw;
  }

  .text-square h2 {
    font-size   : 2.5vw;
    margin-left : 0.5rem;
    padding-left: 0.5rem;
    padding-top : 20px
  }


}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .back-profile {
    background-image: url(/public/img/new_profile.jpg); 
    background-size : 100% 100%;
    height          : 20rem;
    margin-bottom   : 2rem

  }

  header h1.page-title {
    font-size: 3vw;
  }

  header a {
    padding-top: 0.5vw;
    font-size  : 2vw;
  }

  .left-column h2 {
    font-size: 3vw;
  }

  .text-square h2 {
    font-size   : 2.5vw;
    margin-left : 0.5rem;
    padding-left: 0.5rem;
    padding-top : 20px
  }

}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .back-profile {
    background-image: url(/public/img/new_profile.jpg); 
    background-size : 100% 100%;
    height          : 30rem;
    margin-bottom   : 3rem

  }

  header h1.page-title {
    font-size: 3vw;
  }

  header a {
    padding-top: 0.5vw;
    font-size  : 2vw;
  }

  .left-column h2 {
    font-size: 3vw;
  }

  .text-square h2 {
    font-size   : 2.5vw;
    margin-left : 0.5rem;
    padding-left: 0.5rem;
    padding-top : 25px
  }


}



/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  .back-profile {
    background-image: url(/public/img/new_profile.jpg); 
    background-size : 100% 100%;
    height          : 40rem;
    margin-bottom   : 3rem

  }

  header h1.page-title {
    font-size: 2vw;
  }

  header a {
    padding-top: 0.5vw;
    font-size  : 1.5vw;
  }

  .left-column h2 {
    font-size: 2.5vw;
  }

  .text-square h2 {
    font-size   : 1.5vw;
    margin-left : 0.5rem;
    padding-left: 0.5rem;
    padding-top : 33px
  }

}
.profile-pic img {

  width        : 20%;
  top          : 80%;
  right        : 12vh;
  border-radius: 50%;
}

main {
  width: 90%;

}

.row {
  display       : flex;
  flex-wrap     : wrap;
  flex-direction: row;
  margin-left   : 1.5rem;
  margin-right  : 1.5rem;

}

.justify-center {
  justify-content: center;
}


.left-column {
  flex                 : 0 0 15%;
  display              : flex;
  font-size            : 5rem;
  justify-content      : flex-end;
  /*    padding-right  : 2rem;
  border-right         : 0.35rem solid #2a607c;*/
  margin-bottom        : 1vw;
  padding-top          : 20px;

}

.left-column h2 {
  font-weight: bold;
  color      : #2a607c;
  text-align : right;
}

.right-column {
  flex           : 0 0 85%;
  display        : flex;
  flex-direction : row;
  justify-content: center;
  flex-wrap      : wrap;
  color          : var(--heliotrope);
  text-align     : justify;
  margin-bottom  : 1vw;
}

.small-square {
  flex          : 0 0 33%;
  font-size     : 2.0rem;
  text-align    : center;
  padding-left  : 1rem;
  padding-top   : 1rem;
  padding-right : 1rem;
  padding-bottom: 1rem;
}

.big-square {
  flex          : 0 0 100%;
  font-size     : 2rem;
  padding-left  : 1rem;
  padding-top   : 1rem;
  padding-right : 1rem;
  padding-bottom: 1rem;
  outline       : 0.25rem solid turquoise;
  outline-offset: -10px;
}

.small-square:hover,
.big-square:hover {
  cursor: pointer;
  filter: contrast(200%);

}


img {
  display  : block;
  max-width: 100%;
  height   : auto;
  position : relative;
}

.img-wrapper {
  overflow: hidden;
  position: relative;
  outline : 0.1rem solid turquoise;
}

.img-wrapper p {
  position  : absolute;
  background: rgb(120, 226, 216);
  bottom    : 5%;
  left      : 0.5%;
  padding   : 1%;
  font-size : 1vw;
}

.btn-pic {
  padding: 0%;
}

.contact-square a {
  color          : -webkit-link;
  cursor         : pointer;
  text-decoration: underline;
}

.contact-square {
  display              : flex;
  flex                 : 0 0 100%;
  font-size            : 1.5vw;
  justify-content      : space-between;
  padding-left         : 3rem;
  padding-right        : 1rem;
  /*    padding-bottom : 1rem;*/
  padding-top          : 15px;
  align-items          : center;

}

input[type=button] {
  display: none;
}